.superWrapper{
    width: 100vw;
    height: 100vh;
}


.wrapper{
    background-color: #f3f3f3;
    width: 900px;
    height: 500px;
    left: 0;
    right: 0; 
    margin: 0;
    margin-left: auto; 
    margin-right: auto;
    position: relative;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    border-radius: 10px;
    scroll-behavior: smooth;
    overflow-x: hide;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    gap: 0px 0px;
    grid-template-areas:
      ". .";
}

.optionIcon{
    font-size: 180px;
}

.buttonText{
    color:#173e43;
    font-size: 35px;
    text-transform: none;
}

.firstOption{
    margin: auto;
}

.secondOption{
    margin: auto;
}