.wrapper{
    font-family: 'Roboto', sans-serif;
    background-color: #f3f3f3;
    width: 900px;
    height: 500px;
    left: 0;
    right: 0; 
    margin: 0;
    margin-left: auto; 
    margin-right: auto;
    position: relative;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    border-radius: 10px;
    scroll-behavior: smooth;
    overflow-x: hidden;
    display: grid;
    grid-template-columns: 2fr 1fr;
     grid-template-rows: 1fr;
     gap: 0px 0px;
     grid-template-areas:
    ". .";
}

.name{
    width: 440px;
    display: inline;
}

.leftSide{
    width: 460px;
    height: 480px;
    border-radius: 10px;
    margin: auto;
}
.forename{
    width: 440px;
    display: inline;
}

.noteIcon{
    text-align: center;
    font-size: 140px;
    padding-top: 30%;
}

.noteLabel{
    text-align: center;
    font-size: 20px;
    padding-top: 20px;
    color: #173e43;
}

.rightSide{
    position: relative;
    left: -50px;
}