.superWrapper{
    width: 100vw;
    height: 100vh;
    font-family: 'Roboto', sans-serif;
    overflow: hidden;
}


.wrapper{
    background-color: #f3f3f3;
    width: 900px;
    height: 500px;
    left: 0;
    right: 0; 
    margin: 0;
    margin-left: auto; 
    margin-right: auto;
    position: relative;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    border-radius: 10px;
    scroll-behavior: smooth;
    overflow-x: hide;
}