.wrapper{
    width: 1000px;
    display: grid;
    margin: auto;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 4fr;
    gap: 0px 50px;
    grid-template-areas:
      "title title"
      "l r";
  }

  .title{
      text-align: center;
      padding-top: 30px;
      grid-area: title;
  }

  .leftSide{
      grid-area: l;
      font-family: 'Roboto', sans-serif;
  }

  .rigthSide{
    grid-area: r;
    font-family: 'Roboto', sans-serif;
  }

.mainTitle{
    color: "#F1B24A";
    font-size: 50px;
    font-family: 'Oswald', sans-serif;
}

.description{
    font-size: 20px;
    color: #164A41;
}

.links{
    font-size: 20px;
    color: #4D774E;
}

.infoBlock{
    padding-bottom: 20px;
}
.blockTitle{
    font-size: 30px;
    color: #164A41;
}

.blockDescription{
    font-size: 20px;
    color: #164A41;
}

.blockLink{
    text-decoration: none;
    font-size: 20px;
}

a{
    text-decoration: none;
}