.clearDiv{
    clear: both;
}

.wrapper{
    background-color: #F3F3F3;
    overflow-x: auto;
    overflow-y: visible;
}

.mainPhoto{
    position: relative;
    margin-left: auto;
    margin-right: auto;
}

.photoContainer{
    margin-top: 64px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 1000px;
    height: 600px;
    overflow-x: hidden;
    overflow-y: hidden;
}

.textContainer{
    padding-top: 30px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 1000px;
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-template-rows: 1fr;
    gap: 0px 0px;
    grid-template-areas:
      "Left Rigth";  
}

.profileName{
    font-family: 'Oswald', sans-serif;
    color: #3FB0AC;
    font-size: 40px;
    grid-area: Left;
}

.profileMainText{
    grid-area: Rigth;
    font-family: 'Roboto', sans-serif;
    font-weight: 200;
}

.photoGrid{
    padding-top: 60px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 1000px;
    height: 750px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
      ". . . ."
      ". . . ."
      ". . . .";
}

