.wrapper{
    display: grid;
    grid-template-columns: 1fr 2fr 2fr 1fr;
    grid-template-rows: 1fr 4fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
      ". . . ."
      ". text text ."
      ". . . .";
}

.infoPhoto{
    width: 520px;
    height: 360px;
    border-radius: 10px;
}

.infoPhotoDiv{
    grid-area: photo;
    vertical-align: middle;
}

.mainText{
    vertical-align: middle;
    grid-area: text;
    color: #f3f3f3;
    font-size: 25px;
    height: 360px;
    width: 1000px;
}

h2{
    color: #F1B24A;
    padding-bottom: 10px;
    font-size: 36px;
}

.newsTitle{
    font-size: 28px;
    font-weight: 500;
}

.newsDescription{
    font-size: 22px;
    font-weight: 100;
    color: #f3f3f3;
}

.newsItem{
    padding-bottom: 30px;
}