.superWrapper{
    width: 100vw;
    height: 100vh;
    font-family: 'Roboto', sans-serif;
    overflow: hidden;
}


.wrapper{
    background-color: #f3f3f3;
    width: 900px;
    height: 500px;
    left: 0;
    right: 0; 
    margin: 0;
    margin-left: auto; 
    margin-right: auto;
    position: relative;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    border-radius: 10px;
    scroll-behavior: smooth;
    overflow-x: hide;
}

.headerText{
    font-size: 30px;
    color:#173e43;
    text-align: center;
    overflow-x: hide;
}

.mainText{
    font-size: 15px;
    color: #173e43;
    text-align: center;
    overflow-x: hide;
}

.mainTextEmoji{
    font-size: 30px;
    text-align: center;
    overflow-x: hide;
}

.mainTextEmojiAlternative{
    padding-top: 15px;
    font-size: 40px;
    text-align: center;
    overflow-x: hide;
}

.googleLink{
    position: relative;
    overflow-x: hide;
    padding-left: 50px;
}

.Link{
    position: relative;
    overflow-x: hide;
    padding-left: 50px;
    padding-bottom: 15px;
}


.multilineText{
    position: relative;
    overflow-x: hide;
    padding-left: 50px;
    padding-top: 15px;
}

.topText{
    padding-top: 30px;
}

.submitButton{
    padding-top: 0px;
    padding-left: 300px;
}

.submitButtonAlt{
    padding-top: 15px;
    padding-left: 300px;
}