.wrapper{
    top: 400vh;
    height: 100vh;
    max-width: 100%;
    background-color: #E3E3E3;
    font-family: 'Roboto', sans-serif;
}

.infoPhoto{
    width: 600px;
    height: 400px;
    display: inline;
}

.infoPhotoDiv{
    display: inline;
}

.mainText{
    display: inline;
    width: 300px;
}

.infoTitle{
    font-size: 25px;
    position: relative;
    left: 10px;
    top: 10px;
    color: #E3E3E3;
}

.infoAddress{
    font-size: 20px;
    position: relative;
    left: 10px;
    top: 10px;
    color: #E3E3E3;
}

.infoTip{
    position: absolute;
    vertical-align: middle;
    right: 0px;
    z-index: 999;
    width: 200px;
    height: 150px;
    background-color: #4D774E;
}

.location{
    z-index: 800;
    padding-top: -200px;
}

.locationIcon{
    text-align: center;
    position: relative;
    top: 20px;
}
